@import './fonts/fonts.css';
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #E5E5E5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none !important;
    list-style: none;
}

body{
    background: #E5E5E5;
}
.accordion-button{
    border: none !important;
    outline: none;
    box-shadow: none !important;
    background: #7177F1 !important;
    color: #FFFFFF !important;
}
.accordion-button.collapsed{
    background: #FFFFFF !important;
    color: #7177F1 !important;
}
.header__nav-logo::before{
    content: url('./images/logo\ light.png');
}
@media(prefers-color-scheme: dark) {
    body{
        background: #151437;
    }
    .header__nav-logo{
        content: url('./images/logo\ dark.png');
    }
    .header__content-text , .header__toggler-icon , .main__about-description, .footer__link, .footer__icon-insta, .footer__icon-tele{
        color: #ffffff !important;
    }
    .main__about-text{
        background:#7177F1;
        color: #ffffff;
    }
    .main__about-content{
        background: #151437 !important;
    }
    .accordion-button.collapsed{
        background: #151437 !important;
    }
    .accordion-item{
        border: none;
        background-color: #151437 !important;
    }
    .main__works-link{
        background: #151437 !important;
    }
    .main__works-content{
        background: #151437 !important;
    }
    .main__works-link.active{
        
        background: #7177F1 !important;
    color: #FFFFFF !important;
    }
    .main__works-text{
        color: #ffffff !important;
    }
}
@media(max-width:1164px){
        .rectangle , .shape{
        display: none !important;
    }
    .main__works-title{
        color: #7177F1 !important;
    }
    .footer__all{
        margin-top: 30px;
    }
}
@media(max-width:992px){

    .header__nav-link , .header__nav-btn{
        margin-right: 0 !important;
        margin-bottom: 15px !important;
    }
    .main__about-img{
        width: 100% !important;
    }
    .main__about-content{
        margin-top: 30px !important;
    }
    .main__about-text{
        padding: 20px !important;
        left: 100px !important;
        top: -15px !important;
    }
    .accordion{
        margin-bottom: 35px !important;
    }
    .main__works-content{
        margin-top: 30px !important;
    }
    .capabil{
        display: none !important;
    }
    .nav {
        margin-bottom: 25px !important;
    }
    .website_row{
        flex-direction: column-reverse;
    }
    .main__about{
        margin-top: 50px !important;
    }
    .main__about-img{
        margin-top: -p30x;
    }
    .main__about-content{
        padding-bottom: 55px !important;
    }
    .footer__all{
        margin-top: 10px;
    }
    .imgdiv{
        margin-top: 15px !important;
    }
    .caparow{
        margin-top: -40px !important;
    }
    .main__about-content{
        padding: 40px 30px 45px 30px !important;
    }
}
@media(max-width:768px){
    .main__works-img{
        object-fit: contain !important;
    }
    .main__works-name{
        margin-top: 20px !important;
        
    }
}
@media(max-width:496px){
.footer__list, .footer__number{
    flex-direction: column !important;
    padding: 0;
    margin-bottom: 5px !important;
}
.list, .footer__icon-insta, .number, .footer__icon-tele{
    margin-left: 20px !important;
}
.footer__icons{
    justify-content: start !important;
}
.footer__all-end{
    align-items: flex-start !important;
}
.main__about-title{
    font-size: 35px !important;
    line-height: 25px !important;
}
}
@media(max-width:432px){
.header__content-skills{
    flex-wrap: wrap !important;
    justify-content: center !important;
    gap: 15px;
}
.capabil_list{
    height: 550px !important;
}
.header__content-title{
    font-size: 40px !important;
    line-height: 48px !important;
}
.main__about-description {
    font-size: 17px !important;
}
.main__works-title{
    font-size: 37px !important;
    line-height: 43px !important;
}
.main__works-link {
    font-size: 17px !important;
    line-height: 20px !important;
}
.main__works-text{
    font-size: 17px !important;
    line-height: 24px !important;
}
.capabilities__title {
    font-size: 35px !important;
    line-height: 44px !important;
}
.main__about-title {
    line-height: 45px !important;
    font-size: 31px !important;
}
.footer__icon-tele, .footer__icon-insta {
    font-size: 25px !important;
}
.aboutme {
    font-size: 20px !important;
}
}
@media(max-width:385px){
    .capabil_list{
        height: 599px !important;
    }
    .main__about-title {
        font-size: 29px !important;
    }
}
@media(max-width:375px){
    .header__skills-title{
        font-size: 16px !important;
        
    }
    .header__content-title{
        text-align: center !important;
        font-size: 35px !important;
        line-height: 45px !important;
    }
    .main__about-link{
        margin: 0 auto !important;
    }
}